import React, { Component } from 'react';
import './appDetail.css'
import {Link} from 'react-router'
import config from '../config/config'

//var paths = require('../config/paths');

class App extends Component {
  constructor(props){
    super(props)
    this.state = {
      list:[],
      versionObj:undefined
    }
  }

  componentDidMount(){
    this._fetchData()
  }

  async _fetchData(){
    const {appName} = this.props.location.query
    let response = await window.fetch(config.baseUrl+'getapppatchlist',{
      method:'POST',
      body:JSON.stringify({
        appName:appName
      }),
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
      }
    })
    let responseJson = await response.json()
    if(responseJson.code === 0){
      let resultList = responseJson.result.patchs
      let versionObj = responseJson.result.versionObj
      console.log(resultList)
      if(resultList && resultList.length > 0){
        this.setState({
          list:resultList,
          versionObj:versionObj
        })
      }
    }
  }



  _renderApp(name,icon){
    var list = this.state.list
    console.log('list',list)
    var components
    if(this.state.list.length > 0){
      components = list.map((item,index)=>{
        return(
            <li key={index} style={{marginLeft:'6%',height:'30%',marginTop:30}}>
              <Link to={{pathname:'patchDetail',query:{appName:name,appVersion:item.appVersion,appIcon:icon}}}>
                <div style={{display:'flex',flexDirection:'column',width:200,height:'100%',borderRadius:2,backgroundColor:'white'}}>
                  <div className='havePatchContent'>{item.appVersion}<div style={{color:'white',fontSize:13,marginTop:10,opacity:0.8}}>App Version</div></div>
                  <span style={{alignSelf:'center',marginTop:15,color:'#666666'}}><span style={{color:'#333333'}}>{this.state.versionObj[item.appVersion]}</span></span>
                  <span style={{alignSelf:'center',marginTop:15,color:'#666666'}}><span style={{color:'#333333'}}>{(item.create_at/1000).timeDiff()}</span></span>
                </div>
              </Link>
            </li>
        )
      })
    }
    return components
  }

  render() {
    const {appName,appIcon} = this.props.location.query
    console.log('app',appName,appIcon)
    return (
        <div style={{display:'flex',width:'100%',height:'100%',backgroundColor:'#f2f2f2'}}>
          <div style={{display:'flex',flexDirection:'column',width:200,height:'100%',borderRadius:4,backgroundColor:'white',justifyContent:'center',alignItems:'center'}}>
            <img style={{width:100,height:100}} src={appIcon}/>
            <Link to={{pathname:'AddPatch',query:{appName:appName}}} style={{color:'#333333',fontSize:20,marginTop:100}}>{appName}</Link>
          </div>
          <ul style={{height:'100%',display:'flex',flexWrap:'wrap'}}>
            {this._renderApp(appName,appIcon)}
            <li style={{marginLeft:'6%',height:'30%',marginTop:30}}>
              <div style={{display:'flex',flexDirection:'column',width:200,height:'100%',borderRadius:2,backgroundColor:'white'}}>
                <Link to={{pathname:'AddPatch',query:{appName:appName}}} className='patchContent'>App</Link>
              </div>
            </li>
          </ul>
        </div>
    );
  }
}

Number.prototype.timeDiff = function(){
  var d_minutes,d_hours,d_days;
  var timeNow = parseInt(new Date().getTime()/1000);
  var d;
  d = parseInt(timeNow - this);
  d_days = parseInt(d/86400);
  d_hours = parseInt(d/3600);
  d_minutes = parseInt(d/60);
  if(d_days>0 && d_days<4){
    return d_days+"";
  }else if(d_days<=0 && d_hours>0){
    return d_hours+"";
  }else if(d_hours<=0 && d_minutes>0){
    return d_minutes+"";
  }else if(d_minutes <= 0 && d >= 0){
    return ""
  }else{
    var s = new Date(this*1000);
    //s.getFullYear()+"";
    return (s.getFullYear()+"" + (s.getMonth() + 1)+""+s.getDate()+"")
  }
}

export default App;



